





















































import { InKink, InKinkCategory } from "@/types/kinks";
import { Rating } from "@/types/ratings";
import { Vue, Component, Prop } from "vue-property-decorator";
import { showDialog } from "./Dialogs/dialog";
import CommentDialog from "./Dialogs/CommentDialog.vue";
import PromptDialog from "./Dialogs/PromptDialog.vue";

@Component
export default class Kink extends Vue {
  @Prop() kink!: InKink;
  @Prop() category!: InKinkCategory;
  @Prop() ratings!: Rating[];
  @Prop({ default: 'en' }) language!: 'en' | 'fr';

  get displayName(): string {
    return this.language === 'fr' && this.kink.name_fr ? this.kink.name_fr : this.kink.name;
  }

  displaySubcategoryName(subcategory: string): string {
    if (this.language === 'fr' && this.category.subcategories_fr) {
      const index = this.category.subcategories.indexOf(subcategory);
      if (index !== -1 && this.category.subcategories_fr[index]) {
        return this.category.subcategories_fr[index];
      }
    }
    return subcategory;
  }

  displayComment(): string {
    if (this.language === 'fr' && this.kink.comment_fr) {
      return this.kink.comment_fr;
    }
    return this.kink.comment || '';
  }

  async edit(): Promise<void> {
    const result: false | string = await showDialog(PromptDialog, {
      title: 'Edit kink',
      inputLabel: 'Kink name:',
      value: this.kink.name,
    });
    if (result !== false) {
      if (!result) this.remove();
      else this.kink.name = result;
    }
  }

  remove(): void {
    this.$emit("remove");
  }

  async addComment(): Promise<void> {
    await showDialog(CommentDialog, { category: this.category, kink: this.kink, language: this.language });
    if (!this.kink.comment?.trim() && !this.kink.comment_fr?.trim()) {
      this.removeComment();
    }
  }

  removeComment(): void {
    this.$set(this.kink, "comment", undefined);
    this.$set(this.kink, "comment_fr", undefined);
  }
}
