






import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class DownloadButton extends Vue {
  @Prop({ default: 'en' }) language!: 'en' | 'fr';

  get buttonText(): string {
    return this.language === 'fr' ? 'Tlcharger' : 'Download';
  }

  emitClick(): void {
    this.$emit('click');
  }
}
