









import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class ExportButton extends Vue {
  @Prop() loading!: boolean;
  @Prop({ default: 'en' }) language!: 'en' | 'fr';

  get buttonText(): string {
    if (this.loading) {
      return this.language === 'fr' ? 'Envoi en cours' : 'Uploading';
    }
    return this.language === 'fr' ? 'Exporter' : 'Export';
  }

  emitClick(): void {
    if (!this.loading) {
      this.$emit('click');
    }
  }
}
